import React from 'react';
import { Container, Row, Col } from 'react-bootstrap'
import Card from '../Components/card';

import GoogleMapReact from 'google-map-react';

import MapStyle from '../MapStyle'

function Home() {
  const defaultProps = {
    center: {
      lat: 1.3899744840033657,
      lng: 103.89513906150269
    },
    zoom: 11
  };


  return (

    <Row>
      <Col style={{ paddingRight: '0px' }}>

        <div class='map'>
          <GoogleMapReact

            options={{
              styles: MapStyle,
            }}
            bootstrapURLKeys={{ key: "AIzaSyDsbxzcnUKnnyQ3USXKHTaEF1Fq3YJxokc" }}
            defaultCenter={defaultProps.center}
            defaultZoom={defaultProps.zoom}
          >

          </GoogleMapReact>


        </div>


        <div style={{ paddingRight: '20px', paddingTop: '10px', paddingLeft: '20px' }}>
          <Card>
            <div class="social-media">
            <span className={'text value'}>PLACEHOLDER </span> 
            </div>
          </Card>
        </div>



      </Col>
      <Col style={{ paddingLeft: '0px' , paddingRight: '20px'}}>


        <div style={{marginRight: '20px'}}>

    
        <Card>

          <div class={'right-dashboard'}>


            <div class='inner-card'>
              <div class='widget-container'>
                <span className={'text value'}>PLACEHOLDER </span> 
              </div>
            </div>


            <div class='inner-card'>
              <div class='widget-container'>
                <span className={'text v'}>PLACEHOLDER </span> 
              </div>
            </div>



            <div class='inner-card'>
              <div class='widget-container'>
                <span className={'text value'}>PLACEHOLDER </span> 
              </div>
            </div>

          </div>
        </Card>
        </div>
      </Col>
    </Row>



  )
}

export default Home;